<template>
  <el-row v-if="userOrders.length !== 0" type="flex" justify="center">
    <el-col :xs="22" :sm="20" :md="18" :lg="16" :xl="12" type="flex" align="middle">
      <p id="order_title" align="left">
        <img src="@/assets/icons/routers/list.svg" id="order_bag" alt="">
        ИСТОРИЯ ПОКУПОК
      </p>
      <div v-if="userOrders">
        <v-divider class="secondary"></v-divider>
        <el-row
          v-for="order in userOrders" :key="order.id"
          type="flex"
          justify="center"
          class="order"
          style="flex-wrap: wrap">
          <el-col :span="24" align="left">
            <el-row
              type="flex"
              justify="center"
              class="mt-3 mb-3"
              style="flex-wrap: wrap; align-items: center">
              <el-col :xs="6" :sm="3" :md="3" :lg="2" :xl="2">
                <el-switch
                  type="text" class="pl-4 pb-1"
                  v-model="order.showDetails">
                  Показать детали
                </el-switch>
              </el-col>
              <el-col :xs="18" :sm="21" :md="21" :lg="22" :xl="22" class="mb-2">
                <el-tag type="info">
                  ИД: {{ order.id }}
                </el-tag>
                <el-tag type="info">
                  {{ order.checkoutDate | date }}
                </el-tag>
                <el-tag type="info">
                  {{ order.totalPrice }} {{ order.currency }}
                </el-tag>
                <el-tag v-if="order.paymentDate" type="danger">
                  ОПЛАЧЕНО
                </el-tag>
                <el-tag v-if="order.paymentMethod === 'On receipt'" type="danger">
                  ОПЛАТА ПРИ ПОЛУЧЕНИИ
                </el-tag>
                <!--TODO: process all statuses!!!-->
                <el-button type="text" class="info--text">
                  <!--Status:-->
                  <el-tag type="danger" v-if="order.status === 'payPending'">
                    Статус: не оплачено
                  </el-tag>
                  <el-tag type="info" v-if="order.status === 'sentPending'">
                    Статус: ожидает отправки
                  </el-tag>
                  <el-tooltip
                    class="item"
                    effect="dark"
                    placement="top-start"
                    v-if="order.status === 'payPending' || order.status === 'sentPending'"
                    :content="order.status === 'payPending' ? statusTooltips.payPending : statusTooltips.sentPending">
                    <v-icon class="pb-1 info--text">lightbulb_outline</v-icon>
                  </el-tooltip>
                </el-button>
                <!--PAY NOW-->
                <pay-now-dialog
                  :orderId="order.id"
                  :orderItems="order.products"
                  :amount="order.totalPrice"
                  v-if="order.status === 'payPending'">
                </pay-now-dialog>
              </el-col>
            </el-row>
            <!--DEATAILS-->
            <div v-show="order.showDetails === true">
              <el-row type="flex" justify="center" style="flex-wrap: wrap;">
                <el-col :xs="20" :sm="10" :md="10" :lg="10" :xl="10">
                  <div v-if="order.shipping">
                    <p class="info_title">
                      <v-icon class="info--text">person_pin_circle</v-icon>
                      Контакты:
                    </p>
                    <p>
                      Имя: {{ order.buyer.firstname }}<br>
                      Фамилия: {{ order.buyer.lastname }}<br>
                      Email: {{ order.buyer.email }}<br>
                      Телефон: {{ order.buyer.phone }}<br>
                    </p>
                    <p>
                      <v-icon class="info--text">monetization_on</v-icon>
                      <span class="info_title">
                        Метод оплаты:
                      </span><br>
                      {{ order.paymentMethod }}
                    </p>
                  </div>
                </el-col>
                <el-col :xs="20" :sm="10" :md="10" :lg="10" :xl="10">
                  <div v-if="order.shipping">
                    <p class="info_title">
                      <v-icon class="info--text">location_on</v-icon>
                      Доставка:
                    </p>
                    <p>
                      Страна: {{ order.shipping.country }}<br>
                      Город: {{ order.shipping.city }}<br>
                      Улица: {{ order.shipping.street }}<br>
                      Здание: {{ order.shipping.build }}<br>
                      Дом: {{ order.shipping.house }}<br>
                      Почтовый код: {{ order.shipping.postCode }}<br>
                    </p>
                    <p>
                      <v-icon class="info--text">airplanemode_active</v-icon>
                      <span class="info_title">
                        Способ доставки:
                      </span> <br>
                      {{ order.deliveryMethod }}
                    </p>
                  </div>
                </el-col>
              </el-row>
              <el-row type="flex" justify="center" style="flex-wrap: wrap">
                <!--Standard looping is not working-->
                <el-col :span="20">
                  <div>
                    <v-icon class="info--text">shopping_cart</v-icon>
                    <span class="info_title">
                      Товары:
                    </span>
                  </div>
                  <div v-for="i in 10" :key="i">
                    <div v-if="order.products[i-1]">
                      Артикул:
                      <el-tag size="mini" type="info">
                        {{ order.products[i-1].SKU }}
                      </el-tag>
                      <p>
                        Название: {{ order.products[i-1].title }}<br>
                        Цена: {{ order.products[i-1].price }} {{ order.products[i-1].currency }}<br>
                        Количество: {{ order.products[i-1].qty }}
                      </p>
                    </div>
                  </div>
                  <p v-if="order.comments">
                    <span>
                      <i class="el-icon-warning"></i>
                      Комментарии:
                    </span>
                    {{ order.comments }}<br>
                  </p>
                </el-col>
              </el-row>
            </div>
            <v-divider class="secondary"></v-divider>
          </el-col>
        </el-row>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import PayNowDialog from '@/components/shop/PayNowDialog'

export default {
  name: 'OrdersHistory',
  components: {PayNowDialog},
  data () {
    return {
      statusTooltips: {
        payPending: `Если вы уже оплатили товар, то необходимо дождаться подтверждения платежа с PayPal.
                     После этого статус вашего товара автоматически сменится на оплачено.
                     Если у Вас возникли трудности, свяжитесь с нами по контактному номеру`,
        sentPending: `Ваша покупка будет отправленая в ближайшее время`
      }
    }
  },
  computed: {
    userOrders () {
      return this.$store.getters.orders
    }
  }
}
</script>

<style scoped lang="scss">
  #order_title {
    color: $color-secondary;
    font-size: 18px;
  }

  #order_bag {
    height: 28px;
    margin-bottom: -5px;
    margin-right: 2px;
  }

  .order {
    color: white;
  }

  .order:before {
    content: "";
    display: block;
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    background: white;
    opacity: .05;
  }

  .info_title {
    font-size: 16px;
    font-weight: 600;
  }
</style>



